.stackTrace {
  position: relative;
}

.stackTrace pre {
  white-space: pre;
  background-color: var(--code-bg);
  color: var(--key-color-base);
  padding: 10px;
  max-height: 320px;
  overflow: scroll;
}

.button {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 4px 7px;
  color: var(--key-color-base);
  background-color: var(--key-bg-base);
  border: 1px solid var(--key-color-base);
  border-radius: 3px;
}
.button:hover {
  cursor: pointer;
  background-color: var(--selection);
  border: var(--selection);
  color: white;
}

.copyMessage {
  position: absolute;
  top: 5px;
  right: 0px;
  background-color: var(--selection);
  color: white;
  padding: 10px;
  border-radius: 2px;
  cursor: none;
}
