.container {
  position: relative;
}

.edit {
  padding: 5px;
  cursor: pointer;
  opacity: 0.5;
  color: var(--key-color-base);
}
.edit:hover {
  opacity: 1;
}

.error {
  position: absolute;
  z-index: 10;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 100%);
  max-width: 250px;
  width: 100%;

  border-radius: 4px;
  padding: 3px 5px;

  color: white;
  background-color: var(--error-highlight);
  font-size: 85%;
  font-family: sans-serif;

  opacity: 0.9;
}
.error::before {
  position: absolute;
  display: block;
  content: ' ';
  z-index: -1;

  left: 50%;
  width: 10px;
  height: 10px;

  background-color: var(--error-highlight);
  transform: translate(-50%, -7px) rotate(45deg);
}
