.key {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;

	/* TODO: remove this once I have a wrapper with size defaults */
	width: 65px;
	height: 65px;

	font-size: 110%;
	border-radius: 5px;
}

.key:not(:hover) .missingValue {
	text-decoration: underline wavy var(--error-highlight);
	text-underline-offset: 4px;
	color: var(--error-highlight);
	font-weight: bold;
}

.missing {
	position: absolute;
	left: 2px;
	bottom: 2px;
	font-size: 80%;

	animation: pulse ease-in-out 1s alternate-reverse;
}
.missing:hover {
	color: rgb(255, 225, 0);
}

@keyframes pulse {
	0% { color: var(--key-color-base) }
	100% { color: var(--key-color-extreme) }
}


.key:hover .code,
.key:hover .behaviour-binding {
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0;
	text-decoration: underline dotted white 2px;
	text-underline-offset: 4px;
}


.keyDefaultTheme {
	color: var(--key-color-base);
	background-color: var(--key-bg-base);
}

.encoder {
	border-radius: 100%;
	background-color: var(--key-bg-base);
	color: var(--key-color-base);
}

.encoderDirection {
	position: absolute;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
	font-size: 50%;
}

.keyDefaultTheme:hover, .encoder:hover {
	background-color: var(--hover-selection);
	transition: 200ms;
	z-index: 1;
}
.key:hover .code, .key:hover .behaviour-binding {
	color: var(--key-color-hover);
}
.key > .code {
	padding: 5px;
}

[data-simple="true"] { font-size: 140%; }
[data-long="true"] { font-size: 60%; }

.params:not([data-is-root="true"])::before { content: '('; opacity: 0.4; font-weight: bold; margin: 2px; }
.params:not([data-is-root="true"])::after { content: ')'; opacity: 0.4; font-weight: bold; margin: 2px; }
.params:not([data-is-root="true"]) .param:not(:last-child)::after { content: ','; }

.code { padding: 0px 4px; margin-left: -2px; margin-right: -2px; }
.code {
	cursor: pointer;
	display: inline-block;
	box-sizing: content-box;
	min-width: 0.5em;
	text-align: center;
	border-radius: 4px;
}
.code:hover {
	background-color: white !important;
	color: var(--hover-selection) !important;
	border-radius: 4px !important;
}

.code * {
	pointer-events: none;
}

.behaviour-binding {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 10px;
  padding: 2px;
  opacity: 0.65;
}

.behaviour-binding:hover {
  cursor: pointer;
  color: var(--hover-selection) !important;
  background-color: var(--key-bg-hover);
  border-radius: 5px 0;
  opacity: 1;
}
