
.wrapper {
  display: inline-block;
  position: absolute;
  z-index: 100;
  bottom: 5px;
  left: 5px;
}

.wrapper label {
  font-size: 75%;
  font-style: italic;
  display: block;
  padding: 0 10px;
}

.githubLink {
  display: inline-block;
  font-size: 110%;
  font-style: italic;
  background-color: white;
  border-radius: 20px;
  padding: 5px 10px;
  text-decoration: none;

  color: royalblue;
}
