.main {
  padding-bottom: 60px;
}

.sidebar {
  display: flex;
  width: 190px;
  flex-direction: column;
  padding: 10px;
  gap: 10px;
  position: fixed;
}

.selectable {
  cursor: pointer;
  background-color: var(--key-bg-base);
}
.selectable:hover {
  border: 4px solid var(--selection);
  box-sizing: border-box;
}

.highlight {
  background-color: var(--selection);
}
.highlight:hover {
  background-color: var(--hover-selection);
}

.comboPanel {
  max-width: 450px;
  margin: 0 auto;
  padding-bottom: 60px;
}

.heading {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid var(--key-bg-base);
  margin: 5px 0;
}

.action {
  cursor: pointer;
  padding: 5px;
  opacity: 0.6;
}
.action:hover {
  opacity: 1;
}

.combo {
  margin: 10px 0 40px;
}

.combo .detail {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.combo h5 {
  margin: 0;
}

.edit {
  color: var(--key-color-base);
}

.remove {
  color: var(--delete);
}

.addComboInput {
  display: none !important;
}
.addComboInput:hover {
  color: white;
  cursor: pointer;
}
.comboInputList:hover .addComboInput {
  display: flex !important;
  position: absolute;
  right: -48px;
  transform: scale(0.5);
}

.active.addComboInput,
.active.comboInput > * {
  background-color: sandybrown;
  color: white;
}
.active.addComboInput {
  display: flex;
}

.field {
  margin: 10px 0;
  display: flex;
}

.field label {
  display: inline-block;
  width: 120px;
  text-align: right;
  margin-right: 10px;
}

.layerList {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.layerList li {
  cursor: pointer;
  display: inline-block;
  padding: 5px 10px;
  margin: 5px;
  border-radius: 30px;
  background-color: var(--key-bg-base);
}
.layerList li[data-selected="true"] {
  background-color: var(--selection);
  color: var(--key-color-hover);
}
.layerList li:hover {
  background-color: var(--hover-selection);
  color: var(--key-color-hover);
}

.floatingMessage {
  background-color: var(--key-bg-base);
  border-radius: 30px;
  color: var(--key-color-base);
  font-size: 80%;
  text-align: center;
  padding: 5px;
  position: absolute;
  bottom: -40px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 110%;
}