button.action {
  display: inline-block;
  cursor: pointer;
  background-color: var(--hover-selection);
  color: white;

  font-size: 16px;
  border: none;
  border-radius: 5px;
  padding: 0 5px;

  height: 30px;
  line-height: 30px;
  vertical-align: middle;
}

button.secondary {
  color: var(--fg);
  background-color: transparent;
}

button.action[disabled] {
  filter: saturate(20%);
  opacity: .5;
  cursor: not-allowed;
}
