
.buildDetails {
  display: inline-flex;
  height: 30px;
  border-radius: 5px;

  font-size: 70%;
  padding: 0 4px;

  background-color: royalblue;
  color: white;
  text-decoration: none;
}

.buildDetails code {
  font-family: monospace;
}

.buildStatus {
  width: 30px;
  height: 30px;
  line-height: 30px;
  font-size: 200%;
  text-align: center;
}

.buildSuccess {
  /* color: var(--selection); */
}

.buildFailed {
  /* color: brown; */
}

.buildPending {
  color: gold;
  animation: pulse 1s linear alternate infinite;
}

@keyframes pulse {
  from { opacity: 0.5; }
  to { opacity: 1; }
}
