.main {
  padding-bottom: 60px;
}

.sidebar {
  display: flex;
  position: fixed;
  width: 180px;
  flex-direction: column;
  padding: 10px;
  gap: 10px;
}

.macro {
  width: 550px;
  margin: 10px auto 40px;
}
.heading {
  display: flex;
  position: relative;
  justify-content: space-between;
  border-bottom: 2px solid var(--key-bg-base);
  margin: 5px 0;
}
.heading h5 {
  margin: 0;
}
.action {
  cursor: pointer;
  padding: 5px;
  opacity: 0.6;
}
.action:hover {
  opacity: 1;
}
.remove {
  color: var(--delete);
}

.macroActions {
  text-align: center;
  opacity: 0.25;
}
.macroActions > * {
  margin: 5px;
}

.macroBindings {
  position: relative;
  width: 450px;
  /* border: 1px solid lightgray; */
  margin: 0 auto;
  padding: 5px;

  display: flex;
  flex-wrap: wrap;
}

.macro:hover .macroActions {
  opacity: 1;
}

.macroBindings > * {
  margin: 5px;
  width: 65px;
  height: 65px;
}

.controlBinding {
  width: 100%;
  height: 30px;
  box-sizing: border-box;
}
.controlBinding:hover {
  background-color: var(--hover-selection);
  color: var(--key-color-hover);
}
.controlBinding > :first-child {
  width: 100% !important;
  height: 100% !important;
}

.macroBindings [draggable] {
  cursor: grab;
}

.actions {
  visibility: hidden;
  position: absolute;
  top: 0px;
  right: 0px;
  font-size: 80%;

  color: var(--key-color-hover);
}
.actions > * {
  margin: 2px;
  opacity: 0.5;
  cursor: pointer;
}
.macroBindings :hover .actions {
  visibility: visible;
  z-index: 10;
}

.actions :hover {
  opacity: 1;
}


.field {
  margin: 10px 0;
  display: flex;
}

.field label {
  display: inline-block;
  width: 120px;
  text-align: right;
  margin-right: 10px;
}

.betaDisclaimer {
  font-size: 80%;
}