
.dropTargets {
  position:absolute;
  top: -5px;
  left: 0px;
  width: 100%;
  /* border: 1px solid red; */
  /* padding: 5px; */
  /* opacity: 0.5; */
  display: flex;
  flex-wrap: wrap;
}

.dropTargetHalf {
  position: relative;
  width: 37.5px;
  height: 65px;
  margin: 0;
}

.dropTargetHalf[data-is-control="true"] {
  width: 450px;
  height: 15px;
}

.dropTargetRow {
  position: relative;
  width: 450px;
  height: 10px;
}
.dropTargetRow.droppable::after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  width: 450px;
  height: 4px;
  background-color: var(--selection);
}
.secondHalf[data-extend="1"] { width: calc(37.5px + 1 * 75px); }
.secondHalf[data-extend="2"] { width: calc(37.5px + 2 * 75px); }
.secondHalf[data-extend="3"] { width: calc(37.5px + 3 * 75px); }
.secondHalf[data-extend="4"] { width: calc(37.5px + 4 * 75px); }
.secondHalf[data-extend="5"] { width: calc(37.5px + 5 * 75px); }

