.droppable.dropTargetHalf:not(.secondHalf):not([data-is-control="true"])::before,
.droppable.secondHalf:not([data-is-control="true"])::after {
  content: '';
  display: block;
  height: 65px;
  width: 4px;
  border-radius: 4px;
  background-color: var(--selection);
}

.dropRowOnly .droppable:not([data-drop-mode="row"])::before,
.dropRowOnly .droppable:not([data-drop-mode="row"])::after {
   visibility: hidden;
 }

.dropTargetHalf:not(.secondHalf):not([data-is-control="true"])::before {
  transform: translate(-50%, 0);
}
.secondHalf:not([data-is-control="true"])::after {
  position: absolute;
  left: 32.5px;
  transform: translate(50%, 0);
}
