:root {
  --bg: white;
  --fg: black;
	--dark-red: #910e0e;
	--dark-blue: #6d99c6;
  --error-highlight: rgb(179, 60, 60);
  --selection: rgb(60, 179, 113);
  --hover-selection: rgba(60, 179, 113, 0.85);
  --delete: rgb(179, 60, 72);
  --delete-faint: rgba(179, 60, 72, 0.6);
  --link: royalblue;
  --link-hover: #6d99c6;
  --key-bg-base: rgba(128, 128, 128, 0.1);
  --key-bg-hover: white;
  --key-bg-highlight: sandybrown;
  --key-color-base: #6f6f6f;
  --key-color-extreme: #313131;
  --key-color-hover: white;
  --code-bg: whitesmoke;
}
html {
  font-family: Quicksand, avenir, sans-serif;
}
html, body {
	width: 100vw;
	height: 100vh;
	overflow: auto;
	padding: 0;
	margin: 0;
  background-color: var(--bg);
  color: var(--fg);

  transition: 300ms ease-in-out;
  transition-property: color, background-color, border-color;
}

body[data-theme="dark"] {
  --bg: #123;
  --fg: white;
  --key-color-base: #b7b7b7;
  --key-color-extreme: #e7e7e7;
  --code-bg: var(--key-bg-base);
}
@media (prefers-color-scheme: dark) {
  body[data-theme="system"] {
    --bg: #123;
    --fg: white;
  }
}

h3 {
  margin-bottom: 10px;
}

a { color: var(--link) }
a:hover { color: var(--link-hover) }

code {
  background-color: var(--key-bg-base);
  border-radius: 2px;
  padding: 4px;
}

#actions {
	position: absolute;
  display: flex;
	bottom: 5px;
	right: 5px;
  gap: 4px;
}

#actions button {
  display: inline-block;
  cursor: pointer;
  background-color: var(--hover-selection);
  color: white;

  font-size: 16px;
  border: none;
  border-radius: 5px;
  padding: 0 5px;

  height: 30px;
  line-height: 30px;
  vertical-align: middle;
}

#actions button[disabled] {
  filter: saturate(20%);
  opacity: .5;
  cursor: not-allowed;
}

#settings-bar {
  position: absolute;
  top: 10px;
  right: 10px;
}

#settings-bar button {
  background: none;
  color: var(--fg);
  border: none;
}
#settings-bar button:hover {
  cursor: pointer;
  color: royalblue;
}

.fa-delete-right::before {
  content: '\f55a';
  transform-origin: center;
  transform: rotateZ(180deg);
  display: inline-block;
}